html {
    box-sizing: border-box;
    font-size: 100%;
}

*, *:before, *:after {
    box-sizing: inherit;
    margin: 0 auto;
    padding: 0;   
}

body{
  height: 100vh;
  width: 100%;
   padding:10px;
  font-family: $heading;
  color: $white;
  justify-content: center;
  background:top/ contain no-repeat $violet url("../img/bg-mobile.svg") ;
}

/*--Handles Header Section--*/

header{
  margin: 10px 0 50px 0 ;
}

.logo{
  height: 40px;
  width: 150px;
}

/*--Handles Button--*/

a{
  text-decoration: none;   
}

.btn{
  background-color: $white;
  color:$violet;
  padding: 10px 70px;
  font-family: $body;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4); 
  transition:  color 1s;
  border-radius: 50px;
}

.btn:hover{
    color: $white;
    background-color: $magenta;
    box-shadow: 5px 5px 10px rgba(2, 2, 2, 0.6);  /* offset-x | offset-y | blur-radius | spread-radius  | color */
    border-radius: 50px;
}

/*Handles Main Content */

.col-auto{
  flex:1;
  align-items: center;
}

.hero-image{
  width: 100%;
  //height: 100%;
  padding: 20px;
  margin: 0 auto;
}

.hero-content{
  padding: 20px;
  text-align: center;
  margin: 0 auto;
  width: 100%;

p{
  font-family: $body;
  font-size: 16px;
  font-weight: normal;
  color: #e0e0e0;
  margin-top: 20px;
  margin-bottom: 50px;

}

h1{
  font-size: 1.5em;
 line-height: 1.2em;
}

}

/* Handles Footer */

footer{
  margin-top: 30px;
 .icon-container{
     flex:1;
     text-align: center;
  }
 }

 /*--Handles Icon Style--*/

.icon-style{
  margin: 5px;
  border-radius: 50%;
  border-style: solid;
  border-width: thin;
  padding: 15px;
   transition: color 1s;
}

i:hover{
color:$magenta;
cursor: pointer;
}

/* Responsivenes Desktop & Tablet */

@media screen and (min-width:640px){

  body{
    max-height: 100%;
     background:center/ cover no-repeat fixed $violet url("../img/bg-desktop.svg") ;
  }

 .main-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero-image{
  max-width: 100%;
}

.hero-content{
  padding: 20px;
  text-align: justify;
  margin: 0 auto;
  max-width: 100%;

  h1{
    font-size: 3em;
  }

 p{
    font-size: 24px;
 }

}

/* Handles Footer */

footer{
  display: flex;

 .icon-container{
    flex:1;
    text-align: right;
  }
}
}

